// stylelint-disable no-duplicate-selectors, selector-no-qualifying-type
//
// Grid examples
//
.docs-example-row {
    .row {
        > .col,
        > [class^="col-"] {
            span {
                display: block;
                padding: .75rem;
                color: rgb(57, 63, 73);
                background-color: rgb(255, 255, 255);
                box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 4px 16px;
                font-size: $font-size-sm;
                border-radius: .25rem;
                margin: 1rem 0;
            }
        }

        &.no-gutters {
            > .col,
            > [class^="col-"] {
                span {
                    border-radius: 0;
                }
            }
        }
    }

    .row+.row {
        margin-top: 1rem;
    }

    .flex-items-top,
    .flex-items-middle,
    .flex-items-bottom {
        min-height: 6rem;
        background-color: rgba(255, 0, 0, .1);
    }
}

.docs-example-row-flex-cols .row {
    min-height: 10rem;
    background-color: rgba(255, 0, 0, .1);
}

.docs-highlight {
    background-color: rgba($docs-primary, .15);
    border: 1px solid rgba($docs-primary, .15);
}

.docs-example + .docs-clipboard {
    margin-top: 1rem;
}

.dev-example {
    position: relative;
}

.type-example {
    margin-top: 1rem;

    .btn-type-clipboard {
        display: block;
        width: 100%;
        border: 0;
        background: transparent;
        outline: none;
        margin: 15px 0;

        > div {
            font-family: inherit;
            padding: 24px 0;
            background-color: #ffffff;
            border-radius: 4px;
            margin-bottom: 8px;
            box-shadow: 0 1px 3px 0 rgba(0,0,0,0.1), 0 1px 2px 0 rgba(0,0,0,0.06);
            font-size: 1.5rem;
            box-sizing: content-box;
            vertical-align: middle;
            color: $headings-color;

            h1, h2, h3, h4, h5, h6 {
                margin-bottom: 0;
                display: inline-block;
            }

            p {
                margin-bottom: 0;
            }
        }

        span {
            font-size: .875rem;
            line-height: 1.5;
            display: inline-block;
            overflow: hidden;
            vertical-align: middle;
            white-space: nowrap;
            text-overflow: ellipsis;
            color: $text-muted;
        }
    }
}
// Grid mixins
.example-container {
    width: 800px;
    @include make-container();
}

.example-row {
    @include make-row();
}

.example-content-main {
    @include make-col-ready();

    @include media-breakpoint-up(sm) {
        @include make-col(6);
    }

    @include media-breakpoint-up(lg) {
        @include make-col(8);
    }
}

.example-content-secondary {
    @include make-col-ready();

    @include media-breakpoint-up(sm) {
        @include make-col(6);
    }

    @include media-breakpoint-up(lg) {
        @include make-col(4);
    }
}


//
// Container illustrations
//
.docs-example-container {
    min-width: 16rem;
    max-width: 25rem;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 1.5rem;
}

.docs-example-container-header {
    height: 3rem;
    margin-bottom: .5rem;
    background-color: lighten($blue, 50%);
    border-radius: .25rem;
}

.docs-example-container-sidebar {
    float: right;
    width: 4rem;
    height: 8rem;
    background-color: lighten($blue, 25%);
    border-radius: .25rem;
}

.docs-example-container-body {
    height: 8rem;
    margin-right: 4.5rem;
    background-color: lighten($docs-primary, 25%);
    border-radius: .25rem;
}

.docs-example-container-fluid {
    max-width: none;
}


//
// Docs examples
//
.docs-example {
    padding-bottom: 2rem;
    border-bottom: 1px solid $border-color;

    + .docs-example {
        margin-top: 3rem;
    }

    .card {
        @include media-breakpoint-up(sm) {
            max-width: 25rem;
        }
    }

    .tab-example-result {
        position: relative;
        @include clearfix();

        @include media-breakpoint-up(sm) {
            margin-right: 0;
            margin-left: 0;
        }

        > .toast {
            display: block;
            opacity: 1;
        }

        +.highlight,
        +.clipboard+.highlight {
            margin-top: 0;
        }

        +p {
            margin-top: 2rem;
        }

        .pos-f-t {
            position: relative;
            margin: -1rem;

            @include media-breakpoint-up(sm) {
                margin: -1.5rem;
            }
        }

        .custom-file-input:lang(es)~.custom-file-label::after {
            content: "Elegir";
        }

        >.form-control {
            +.form-control {
                margin-top: .5rem;
            }
        }

        > .card:last-child {
            margin-bottom: 0;
        }

        > .progress {
            margin-bottom: 0;
        }

        >.nav+.nav,
        >.alert+.alert,
        >.navbar+.navbar,
        >.progress+.progress,
        >.progress+.btn,
        > nav > .breadcrumb,
        > .badge:not(.tag),
        > .btn {
            margin-top: .5rem !important;
            margin-bottom: .5rem !important;
        }

        > .btn-group {
            margin-top: .5rem;
            margin-bottom: .5rem;
            .btn {
                margin: 0;
            }
        }

        > .alert {
            margin: 0;

            + .alert {
                margin-top: 1.25rem;
            }
        }

        >.input-group,
        >.alert {
            .btn {
                margin: 0 !important;
            }
        }

        >.badge {
            margin-right: .5rem;
        }

        >.btn {
            .badge {
                margin-top: 0 !important;
                margin-bottom: 0 !important;
                margin-right: 0 !important;
            }
        }

        >.dropdown-menu:first-child {
            position: static;
            display: block;
        }

        >.form-group:last-child {
            margin-bottom: 0;
        }

        >.close {
            float: none;
        }
    }

}

// Typography
.docs-example-type {
    .table {
        .type-info {
            color: #999;
            vertical-align: middle;
        }
        td {
            padding: 1rem 0;
            border-color: #eee;
        }
        tr:first-child td {
            border-top: 0;
        }
    }

    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin-top: 0;
        margin-bottom: 0;
    }
}

// Contextual background colors
.docs-example-bg-classes p {
    padding: 1rem;
}

// Images
.docs-example>img {
    +img {
        margin-left: .5rem;
    }
}

// Buttons
.docs-example {
    >.btn-group {
        margin-top: .25rem;
        margin-bottom: .25rem;
    }
    >.btn-toolbar+.btn-toolbar {
        margin-top: .5rem;
    }
}

// Forms
.docs-example-control-sizing select,
.docs-example-control-sizing input[type="text"]+input[type="text"] {
    margin-top: .5rem;
}

.docs-example-form .input-group {
    margin-bottom: .5rem;
}

.docs-example>textarea.form-control {
    resize: vertical;
}

// List groups
.docs-example>.list-group {
    max-width: 400px;
}

// Navbars
.docs-example {
    .fixed-top,
    .sticky-top {
        position: static;
        margin: -1rem -1rem 1rem;
    }
    .fixed-bottom {
        position: static;
        margin: 1rem -1rem -1rem;
    }

    @include media-breakpoint-up(sm) {
        .fixed-top,
        .sticky-top {
            margin: -1.5rem -1.5rem 1rem;
        }
        .fixed-bottom {
            margin: 1rem -1.5rem -1.5rem;
        }
    }
}

// Pagination
.docs-example .pagination {
    margin-top: .5rem;
    margin-bottom: .5rem;
}

// Example modals
.modal {
    z-index: 1072;

    .tooltip,
    .popover {
        z-index: 1073;
    }
}

.modal-backdrop {
    z-index: 1071;
}

.docs-example-modal {
    background-color: #fafafa;

    .modal {
        position: relative;
        top: auto;
        right: auto;
        bottom: auto;
        left: auto;
        z-index: 1;
        display: block;
    }

    .modal-dialog {
        left: auto;
        margin-right: auto;
        margin-left: auto;
    }
}

// Example tabbable tabs
.docs-example-tabs .nav-tabs {
    margin-bottom: 1rem;
}

// Popovers
.docs-example-popover-static {
    padding-bottom: 1.5rem;
    background-color: #f9f9f9;

    .popover {
        position: relative;
        display: block;
        float: left;
        width: 260px;
        margin: 1.25rem;
    }
}

// Tooltips
.tooltip-demo a {
    white-space: nowrap;
}

.docs-example-tooltip-static .tooltip {
    position: relative;
    display: inline-block;
    margin: 10px 20px;
    opacity: 1;
}

// Scrollspy demo on fixed height div
.scrollspy-example {
    position: relative;
    height: 200px;
    margin-top: .5rem;
    overflow: auto;
}

.scrollspy-example-2 {
    position: relative;
    height: 350px;
    overflow: auto;
}

.docs-example-border-utils {
    [class^="border"] {
        display: inline-block;
        width: 5rem;
        height: 5rem;
        margin: .25rem;
        background-color: #f5f5f5;
    }
}

.docs-example-border-utils-0 {
    [class^="border"] {
        border: 1px solid $border-color;
    }
}

//
// Code snippets
//
.highlight {
    padding: 0;
    -ms-overflow-style: -ms-autohiding-scrollbar;

    @include media-breakpoint-up(sm) {
        padding: 0;
    }
}

.docs-content .highlight {
    margin-right: (-$grid-gutter-width / 2);
    margin-left: (-$grid-gutter-width / 2);

    @include media-breakpoint-up(sm) {
        margin-right: 0;
        margin-left: 0;
    }
}

//
// Component-Code tabs
//

.docs-example {
    position: relative;

    > .nav-example {
        margin: .5rem 0;

        .nav-item {
            &:not(:last-child) {
                margin-right: .5rem;
            }
        }

        .nav-link {
            padding: .357rem .75rem;
            font-size: .75rem;
            background-color: theme-color("neutral");
            color: color-yiq(theme-color("neutral"));
            border: 1px solid $border-color;

            &:hover {
                border-color: darken($border-color, 3%);
                background-color: darken(theme-color("neutral"), 3%);
            }

            &:active,
            &.active {
                border-color: theme-color("primary");
                color: theme-color("primary");
                background-color: theme-color("neutral");
            }
        }
    }

    .tab-example-html {
        position: relative;
        margin-bottom: 3rem;
    }

    .tab-example-result {
        // padding: 1.25rem;
        // margin-bottom: 3rem;
        // border: 1px solid $border-color;
        // @include border-radius($border-radius);
    }

    .tab-example-html {
        .highlight {
            margin-bottom: 0;
        }
    }

    .btn-clipboard {
        position: absolute;
        top: 1px;
        right: 1px;
        z-index: 10;
        display: block;
        padding: .25rem .5rem;
        font-size: $font-size-sm;
        font-weight: bold;
        cursor: pointer;
        background-color: theme-color("neutral");
        color: color-yiq(theme-color("neutral"));
        border: 0;
        border-left: 1px solid $border-color;
        border-bottom: 1px solid $border-color;
        @include border-radius(0 0 0 4px);

        &:hover {
            color: theme-color("primary");
            background-color: darken(theme-color("neutral"), 3%);
        }

        &:active,
        &.active {
            color: theme-color("primary");
            background-color: theme-color("neutral");
        }
    }
}
