
// Page titles

.docs-page-title {
    position: relative;
    margin-bottom: 1.5rem;

    .heading {
        margin-bottom: 1rem;
        padding: 0;
        font-size: 1.5rem;
        font-weight: 300;
    }
}

.docs {
    background: #FFF;

    .navbar-nav {
        .nav-link {
            font-family: inherit;
            font-weight: 400;
        }

        .nav-link {
            &.active {
                position: relative;

                &:before {
                    content: '';
                    position: absolute;
                    top: 50%;
                    left: 1.5rem;
                    width: 4px;
                    height: 4px;
                    border-radius: 50%;
                    background: $primary;
                    transform: translateY(-50%);
                }
            }
        }
    }
}

// Docs content

.docs-content {
    margin-bottom: 2rem !important;

    >h2[id],
    >h3[id],
    >h4[id] {
        pointer-events: none;

        >div,
        >a {
            pointer-events: auto;
        }

        &::before {
            display: block;
            height: 6rem;
            margin-top: -6rem;
            visibility: hidden;
            content: "";
        }
    }

    >h2:not(:first-child) {
        margin-top: 3rem;
        font-size: 1.5rem;
        font-weight: $headings-font-weight;
    }

    >h3 {
        margin-top: 2.5rem;
        font-size: 1.25rem;
        font-weight: $headings-font-weight;
    }

    >h4 {
        margin-bottom: 1rem;
        font-size: .75rem;
        text-transform: uppercase;
        font-weight: $headings-font-weight;
    }

    > ul li,
    > ol li {
        margin-bottom: .25rem;
    }

    >table {
        width: 100%;
        max-width: 100%;
        margin-bottom: 1rem;

        @include media-breakpoint-down(md) {
            display: block;
            overflow-x: auto;
            -ms-overflow-style: -ms-autohiding-scrollbar; // See https://github.com/twbs/bootstrap/pull/10057
            &.table-bordered {
                border: 0;
            }
        } // Cells
        >thead,
        >tbody,
        >tfoot {
            >tr {
                >th,
                >td {
                    padding: $table-cell-padding;
                    vertical-align: top;
                    border: 1px solid $table-border-color;

                    >p:last-child {
                        margin-bottom: 0;
                    }
                }
            }
        } // Prevent breaking of code (e.g., Grunt tasks list)
        td:first-child>code {
            white-space: nowrap;
        }
    }
}

.docs-sidebar {
    .nav-link {
        padding-left: 0;
        padding-right: 0;
    }
}

//
// Docs sections
//

.docs-page-title {
    margin-bottom: 1rem;
}

.docs-lead {
    color: rgb(59, 69, 78);
    font-weight: 500;

    @include media-breakpoint-up(sm) {
        margin-bottom: 1rem;
    }
}

.docs-text-purple {
    color: $docs-primary;
}

.docs-text-purple-bright {
    color: $docs-primary-bright;
}

// Docs code example tabs

.docs-tabs-example {
    .nav-link i {
        margin-right: 5px;
    }
}



// Typeface palette

.typeface-entry {
    position: relative;
    display: block;
    padding: 2rem;
    background: $card-bg;
    border: 1px solid $card-border-color;
    border-radius: $card-border-radius;
}

.typeface-badge {
    position: absolute;
    top: 10px;
    right: 10px;
}


// Color palette

.color-palette {
    .color-group {
        .color-item {
            .color {
                position: relative;
                display: block;
                padding: 1rem 1rem;
                min-height: 50px;
            }

            &:first-child {
                .color {
                    @include border-top-radius($border-radius);
                }

            }

            &:last-child {
                .color {
                    @include border-bottom-radius($border-radius);
                }

            }

            &:only-child {
                .color {
                    border-radius: $border-radius;
                }

            }
        }
    }

    .color-name,
    .color-code {
        display: block;
    }

    .color-code {
        text-transform: uppercase;
    }

    .color-item-light {
        .color-name {
            color: $gray-800;
        }

        .color-code {
            color: rgba($gray-800, .7);
        }
    }

    .color-item-dark {
        .color-name {
            color: $white;
        }

        .color-code {
            color: rgba($white, .7);
        }
    }

}

// Color switch

.color-switch a {
    width: 1rem;
    height: 1rem;
    margin-right: .25rem;
    border-radius: 50%;
}
