// Sidenav

.docs {
    .sidenav {
        .navbar-nav-docs {
            > .nav-item {
                padding: 0 .5rem;

                > .nav-link {
                    padding-top: .875rem !important;
                    padding-bottom: .875rem !important;
                    border-bottom: 1px dashed $border-color;
                }

                .nav {
                    padding: 1.25rem 0;
                    .nav-item {
                        .nav-link {
                            padding-left: 2.5rem;
                        }
                    }
                }

                .show {
                    .nav {
                        border-bottom: 1px dashed $border-color;
                    }
                }
            }
        }
    }
}

// Toc

.section-nav {
    padding: 0;
}

.toc-sidebar {
    position: fixed;
    top: 0;
}

.toc-entry {
    display: block;

    ul {
        padding-left: 1rem;
    }

    a {
        display: block;
        padding: .125rem 0;
        font-size: .875rem;
        color: $gray-700;

        &:hover {
            color: $gray-600;
            text-decoration: none;
        }
    }
}
